.a{
	height: 100vh;
	display: flex;
	align-items: center;
}
.a-left{
	flex: 1;
	display: flex;
	align-items: center;
	justify-content: center;
	position: relative;
	height: 100%;

}
.a-card{
	width:  60%;
	height: 70vh;
	border-radius: 30px;
	position: relative;
	overflow: hidden;
}
.a-card.bg{
	position: absolute;
	top: 50px;
	left: 50px;
	background: #333;
}
.a-img{
	width: 100%;
	height: 100%;
	object-fit: cover;
}
.a-right{
	flex: 1;
}

.a-award-img{
	width: 120px;
	height: 120px;
	
}
.a-title{
	font-weight: 400;
}
.a-sub{
	margin: 10px 0px;
}
.a-sub2{
	margin: 10px 0px;
	width: 90%;
}
.a-desc{
	font-weight: 300;
	width: 90%;

}
.a-award{
	margin-top: 50px;
	display: flex;
	align-items: center;
	justify-content: space-between;
}
.a-award-text{
	width: 70%;
}
.a-award-title{
	font-weight: bold;
	color: #555;
	margin-bottom: 10px;
	
}
@media screen and (max-width: 480px){
	.a{
		flex-direction: column;
		text-align: center;
		margin-top: 50px;
		
	}
	.a-left{
		width: 100%;
	}
	.a-card{
		height: 30vh;

	}
	.a-card.bg, .a-award{
		display: none;
	}
	.a-right{
		padding: 20px;

	}
}